"use client";
import React from 'react';
import Link from 'next/link';

// BlogCard Component 
export default function BlogCard({ post }) {
  const { 
    slug, 
    title, 
    date, 
    description, 
    category, 
    image, 
    author, 
    readingTime 
  } = post;

  function formatDate(date) {
    try {
      return new Date(date).toLocaleDateString('en-US', {
        year: 'numeric',
        month: 'long',
        day: 'numeric',
      });
    } catch (e) {
      return 'Invalid Date';
    }
  }

  return (
    <div className="blog-post-item bg-white rounded-lg overflow-hidden shadow-sm">
      {/* Featured Image */}
      <div className="blog-card-image">
        <div className="absolute inset-0 bg-gray-200">
          {image && (
            <img 
              src={image} 
              alt={title}
              className="w-full h-full object-cover"
            />
          )}
        </div>
      </div>
      
      {/* Post Content */}
      <div className="p-5">
        {/* Category Tag */}
        <div className="mb-2">
          <span className="inline-block bg-gray-100 rounded-full px-3 py-1 text-xs font-semibold text-gray-700">
            {category}
          </span>
        </div>
        
        {/* Post Meta */}
        <div className="flex items-center justify-between text-xs text-gray-500 mb-2">
          <span>{formatDate(date)}</span>
          <span>{readingTime}</span>
        </div>
        
        {/* Post Title */}
        <h3 className="text-xl font-bold mb-2 hover:text-blue-600 transition duration-200">
          <Link href={`/blog/${slug}`}>
            {title}
          </Link>
        </h3>
        
        {/* Post Description */}
        <p className="text-gray-600 text-sm mb-4 line-clamp-2">{description}</p>
        
        {/* Author */}
        <div className="flex items-center">
          <div className="author-avatar">
            {author.avatar && (
              <img 
                src={author.avatar} 
                alt={author.name}
                className="w-full h-full object-cover"
              />
            )}
          </div>
          <span className="text-sm font-medium">{author.name}</span>
        </div>
      </div>
    </div>
  );
}